* {
    box-sizing: border-box;
    padding: 0;
    margin:0;
}

/* Fix potential body margin issues */
html, body {
    width: 100%;
    min-height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden; /* Prevents any unwanted horizontal scrolling */
}

body {
    overflow-y: auto;
}

.about-page {
    text-align: center;
    /*padding-top: 60px;*/
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
  
  /* Navbar Styles */
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    background-color: #333;
    color: white;
    top:0;
    left:0;
    position: fixed;
    width: 100%;
    z-index:100;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  /* Navigation Links */
  .nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .nav-links li {
    display: inline;
  }
  
  .nav-links a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    padding: 8px 12px;
  }
  
  .nav-links a:hover {
    background-color: #555;
    border-radius: 5px;
  }
  
  /* Hamburger Menu */
  .hamburger {
    font-size: 28px;
    cursor: pointer;
    display: none;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .hamburger {
      display: block;
    }
  
    .nav-links {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      left: 0;
      width: 100%;
      background-color: #333;
      text-align: center;
    }
  
    .nav-links.open {
      display: flex;
    }
  
    .nav-links li {
      padding: 15px;
    }
  }

  /* Form Styles*/ 
.fundraiser-content {
    padding-top: 170px;
    position: relative;
}

@media(max-width:1000px) {
  .fundraiser-content {
    padding-top: 200px;
  }
}

.left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.fundraiser-content p {
    padding-bottom:20px;
}

#firstelement {
    margin-bottom: 0px;
    padding-bottom: 0px;
}

/* CSS in Start.css */

form {
   width:30rem;
}

.fundraiser-form input,
.fundraiser-form textarea,
.fundraiser-form select {
    display: block;
    width: 100%;
    margin: 0 !important; /* Remove all spacing */   
    padding: 6px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
}

.fundraiser-form input {
    display: inline-block;
    margin-top: 0; /* Removes extra space above input */
}

.fundraiser-form label {
    font-weight: bold;
    display: inline-block;
    margin-bottom: 0; /* Remove label bottom margin */
    font-size: 16px;
}

.fundraiser-form input:not(:last-child),
.fundraiser-form textarea:not(:last-child),
.fundraiser-form select:not(:last-child) {
    margin-bottom: 2px; /* Maintain spacing between fields */
}

.fundraiser-form button {
    margin-top: 10px;
    padding: 10px;
    background-color: #4CAF50;
    color: #fff;
    font-size: 18px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.fundraiser-form button:hover {
    background-color: #45a049;
}
